* {
  font-family: "Poppins", sans-serif;
}
.App {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  border: 1px solid black;
  width: 700px;

  background-color: #388574;
  border-radius: 10px;
}

#app-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

#tasksList {
  display: flex;
  flex-direction: column;
}

#addBtn {
  background-color: #282c34;
  color: #ffff;
  border-radius: 20px;
  height: 30px;
  width: 70px;
  margin-left: 20px;
  font-weight: bolder;
}

#taskInput {
  height: 25px;
  width: 400px;
  border: 1px solid black;
  border-radius: 20px;
  flex: 1;
  font-size: 18px;
  text-align: center;
  color: #282c34;
}

.task {
  background-color: #282c34;
  color: #ffff;
  min-width: 450px;
  list-style: none;
  min-height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px 15px;
  border-radius: 10px;
  font-size: 18px;
  margin: 10px 10px 0px -30px;
}

#dltBtn {
  background-color: #ffff;
  color: #282c34;
  border-radius: 20px;
  height: 30px;
  width: 70px;
  margin-left: 20px;
  font-weight: bolder;
}

#dltBtn:hover {
  background-color: red;
  color: white;
}

#edtBtn {
  background-color: #ffff;
  color: #282c34;
  border-radius: 20px;
  height: 30px;
  width: 70px;
  margin-left: 20px;
  font-weight: bolder;
}

#edtBtn:hover {
  background-color: green;
  color: white;
}

.edtDelBtns {
  display: flex;
}
